import React from 'react';
import PropTypes from 'prop-types';

import './buttons.scss';

export const Buttons = ({ handleGetAll, handleGetFive, handleGetRed }) => (
  <div className="buttons">
    <button type="button" onClick={handleGetAll}>
      All goods
    </button>
    <button type="button" onClick={handleGetFive}>
      5 first goods
    </button>
    <button type="button" onClick={handleGetRed}>
      Red goods
    </button>
  </div>
);

Buttons.propTypes = {
  handleGetAll: PropTypes.func.isRequired,
  handleGetFive: PropTypes.func.isRequired,
  handleGetRed: PropTypes.func.isRequired,
};
